'use client';

import React from 'react';
import { useRouter } from 'next/navigation';
import { useAppContext } from '../context/AppContext';
import logo from '../assets/logo.svg';

// Import header styles
import { 
  SharedHeader,
  HeaderContainer,
  HeaderLogo,
  HeaderMenu,
  HeaderLink,
  HeaderIcons,
  CartCount,
  UserGreeting
} from '../styles/HeaderStyles';

interface SharedHeaderProps {
  activePage?: string;
}

const Header: React.FC<SharedHeaderProps> = ({ activePage = '' }) => {
  const router = useRouter();

const navigate = (path: string) => {
  router.push(path);
};
  const { user, cartItems, logout } = useAppContext();
  
  const cartCount = cartItems.reduce((total, item) => total + item.quantity, 0);

  const handleLogout = () => {
    logout();
    navigate('/');
  };

  return (
    <SharedHeader>
      <HeaderContainer>
        <HeaderLogo>
          <a href="#" onClick={(e) => { e.preventDefault(); navigate('/'); }}>
            <img 
              src={logo} 
              alt="Colour My Space Logo" 
              className="logo-image" 
            />
          </a>
        </HeaderLogo>
        
        <HeaderMenu>
          <HeaderLink 
            href="#" 
            active={activePage === 'home'}
            onClick={(e) => { e.preventDefault(); navigate('/'); }}
          >
            Home
          </HeaderLink>
          <HeaderLink 
            href="#" 
            active={activePage === 'shop'}
            onClick={(e) => { e.preventDefault(); navigate('/shop'); }}
          >
            Shop
          </HeaderLink>
          <HeaderLink 
            href="#" 
            active={activePage === 'portfolio'}
            onClick={(e) => { e.preventDefault(); navigate('/portfolio'); }}
          >
            Portfolio
          </HeaderLink>
          <HeaderLink 
            href="#" 
            active={activePage === 'services'}
            onClick={(e) => { e.preventDefault(); navigate('/services'); }}
          >
            Services
          </HeaderLink>
          {user && (
            <HeaderLink 
              href="#" 
              active={activePage === 'orders'}
              onClick={(e) => { e.preventDefault(); navigate('/orders'); }}
            >
              Orders
            </HeaderLink>
          )}
          {user && user.role === 'admin' && (
            <HeaderLink 
              href="#" 
              active={activePage === 'admin'}
              onClick={(e) => { e.preventDefault(); navigate('/admin'); }}
            >
              Admin Dashboard
            </HeaderLink>
          )}
          <HeaderLink 
            href="#" 
            active={activePage === 'about'}
            onClick={(e) => { e.preventDefault(); navigate('/about'); }}
          >
            About
          </HeaderLink>
          <HeaderLink 
            href="#" 
            active={activePage === 'contact'}
            onClick={(e) => { e.preventDefault(); navigate('/contact'); }}
          >
            Contact
          </HeaderLink>
        </HeaderMenu>
        
        <HeaderIcons>
          <button className="nav-icon">
            <i className="fas fa-search"></i>
          </button>
          {user ? (
            <>
              <button className="nav-icon" onClick={handleLogout}>
                <i className="fas fa-sign-out-alt"></i>
              </button>
              <UserGreeting>Hi, {user.name}</UserGreeting>
            </>
          ) : (
            <button className="nav-icon" onClick={() => navigate('/auth')}>
              <i className="fas fa-user"></i>
            </button>
          )}
          <button className="nav-icon" onClick={() => navigate('/cart')}>
            <i className="fas fa-shopping-cart"></i>
            {cartItems.length > 0 && <CartCount>{cartCount}</CartCount>}
          </button>
        </HeaderIcons>
      </HeaderContainer>
    </SharedHeader>
  );
};

export default Header;